@import '../_vars';

.home-box {
    @include gradient-home;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.home-links-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 2;
    padding-top: 50px;
    
    //background-color:rgba(0, 0, 0, 0.308);
}

.particles { //gets called in Animation.js
    position:absolute;
    width: 100%;
    height: 100%;
}

.intro {
    font-family: $font-sand;
    align-self: center;
    text-align: center;
    color: $cyan-d; 
    font-size: 110%;
    h1 {
        color: $cyan-l;
    }
}

@media (min-width: 812px) {
    .home-links-box {
        flex-direction: row;    
    }
}
