.wrapper-svg{min-width:10vw;height:auto}.nav-box{width:auto;height:auto}.nav-svg{width:20px;height:20px;fill-opacity:1;margin:0 auto;display:block}.nav-svg .cls-1{fill:#bce2cf}.nav-svg:hover{fill-opacity:.8;cursor:pointer}.nav-list{display:flex;flex-direction:column;align-items:flex-end;justify-content:center;width:auto;width:10vw}.nav-link{margin:2px;padding:5px;width:auto;background-color:rgba(11,16,12,.432)}.nav-link a{font-family:"Quicksand";font-weight:bold;text-transform:uppercase;-webkit-text-emphasis:none;text-emphasis:none;text-decoration:none;color:#17473d;padding:2px;border:none 2px #bce2cf}.nav-link a:hover{color:#bce2cf;border:solid;box-shadow:0 0 10px #019664;text-shadow:0 0 5px #019664}@media(min-width: 812px){.nav-list{align-items:center}.nav-svg{height:30px;width:30px}}
